@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@400;500&display=swap);
@font-face {
  font-family: "Code Squared";
  src: url(/static/media/Code-Squared.c23ef136.ttf) format("truetype");
  src: url(/static/media/Code-Squared.ff7be27b.eot?#iefix) format("embedded-opentype"),
    url(/static/media/Code-Squared.22f11326.woff2) format("woff2"),
    url(/static/media/Code-Squared.8a7a63f8.woff) format("woff"),
    url(/static/media/Code-Squared.ff7be27b.eot);
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "PrintClearly";
  src: url(/static/media/PrintClearly.6285c105.ttf);
}

@font-face {
  font-family: "DenseLetters";
  src: url(/static/media/DenseLetters.9efca82c.otf);
}

@font-face {
  font-family: "Starkiller";
  src: url(/static/media/Starkiller.317897ff.ttf);
}

@font-face {
  font-family: "StarkillerOutline";
  src: url(/static/media/StarkillerOutline.230bc0b7.ttf);
}

@font-face {
  font-family: "HeroOutline";
  src: url(/static/media/HeroOutline.1959e50e.ttf);
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  color: #ffffff;
  font-family: "Quicksand", sans-serif;
  line-height: 1.66;
}

html {
  scroll-behavior: smooth;
}

body {
  background: #0c0c0c;
}

/* Nav */

#top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 35px 45px;
}

#top p.logo {
  font-family: "Code Squared";
  font-size: 20px;
  color: #ffffff;
  text-shadow: 1px 1px 10px rgba(7, 207, 246, 1);
  margin-bottom: 0;
}

#top ul {
  list-style: none;
  margin-bottom: 0;
}

#top ul a {
  text-decoration: none;
}

#top ul li {
  display: inline;
  padding-left: 16px;
  color: #ffffff;
  letter-spacing: 2px;
  font-size: 16px;
}

#top ul li:hover {
  color: #07cff6;
}

/* Socials */

#social-links {
  position: fixed;
  bottom: 0;
  left: 0;
  padding: 45px 45px 0 45px;
  z-index: 1;
}

#social-links ul {
  list-style: none;
  margin-bottom: 0;
}

#social-links ul a li {
  padding: 6px 0;
}

#social-links ul img {
  height: 25px;
  width: auto;
  -webkit-filter: invert(100%);
  filter: invert(100%);
  transition: transform 200ms;
}

#social-links ul img:hover {
  transform: translate(0, -5px);
  -webkit-filter: invert(74%) sepia(46%) saturate(6280%) hue-rotate(151deg)
    brightness(109%) contrast(94%);
          filter: invert(74%) sepia(46%) saturate(6280%) hue-rotate(151deg)
    brightness(109%) contrast(94%);
}

#social-links ul::after {
  content: "";
  display: block;
  width: 1px;
  height: 90px;
  margin: 0px auto;
  margin-top: 15px;
  background-color: #ffffff;
}

/* Email */

#emailadd {
  position: fixed;
  bottom: 0;
  right: 0;
  padding: 45px 45px 0 45px;
  z-index: 1;
}

#emailadd a {
  -ms-writing-mode: tb-rl;
  writing-mode: vertical-rl;
  color: #ffffff;
  text-decoration: none;
  font-size: 16px;
  letter-spacing: 2px;
  transition: transform 200ms;
}

#emailadd a:hover {
  transform: translate(0, -5px);
  color: #07cff6;
}

#emailadd::after {
  content: "";
  display: block;
  width: 1px;
  height: 90px;
  margin: 0px auto;
  margin-top: 15px;
  background-color: #ffffff;
}

/* HERO SECTION */

#hero {
  height: calc(90vh - 103px);
  align-items: center;
  display: flex;
  margin-bottom: 10vh;
}

#hero h1 {
  font-family: "Code Squared";
  letter-spacing: 2px;
  font-size: 35px;
  padding: 10px 0px 5px 0px;
}

#hero h2 {
  font-family: "Code Squared";
  font-size: 16px;
  color: #07cff6;
  padding: 5px 0px 5px 0px;
}

#hero p {
  font-size: 16px;
}

#hero .info > * {
  margin-bottom: 15px;
}

#hero .img img {
  height: auto;
  width: 100%;
}

#hero .container .row {
  display: flex;
  align-items: center;
}

/* botton */

.link-btn {
  font-size: 16px;
  display: inline-block;
  border: 1px solid #07cff6;
  color: #ffffff;
  text-decoration: none;
  padding: 8px 25px;
  margin-top: 25px;
  letter-spacing: 1px;
  box-shadow: 1px 1px 5px 2px #07cff6;
  transition: box-shadow 500ms;
}

.link-btn:hover {
  text-decoration: none;
  color: #ffffff;
  box-shadow: 1px 1px 10px 2px #07cff6;
}

/* Big title */

.title {
  font-family: "StarkillerOutline";
  font-size: 150px;
  color: #171717;
  margin: 0 50px;
  letter-spacing: 10px;
  margin-bottom: 40px;
}

/* About  */

#about .container {
  display: flex;
  align-items: center;
  justify-content: center;
}

#about .section-title {
  text-align: left;
}

#about .section-title .title {
  animation: slideToRight 1s linear infinite;
  animation-play-state: paused;
  animation-delay: calc(var(--scroll) * -1s);
  animation-iteration-count: 1;
  animation-fill-mode: both;
  display: inline-block;
  letter-spacing: 10px;
}

.about {
  height: 800px;
}

.about-image {
  display: flex;
  justify-content: center;
  align-content: center;
}

.profile {
  margin: auto 0;
  width: 300px;
  height: 310px;
  background: linear-gradient(
    180deg,
    rgba(7, 207, 246, 0.17) 0%,
    rgba(255, 255, 255, 0) 100%
  );
  border: 5px solid #07cff6;
  -webkit-filter: drop-shadow(20px 4px 31px #07cff6);
          filter: drop-shadow(20px 4px 31px #07cff6);
}

.profile canvas {
  width: 300px !important;
  height: 300px !important;
}

.about-text {
  display: flex;
  flex-wrap: wrap;
  align-content: center;
}

.kodigo {
  color: #07cff6;
  font-family: "Code Squared";
  font-size: 11px;
  text-decoration: none;
}

.about h3 {
  font-family: "Code Squared";
  padding: 10px 0px 5px 0px;
  font-size: 10px;
  color: #07cff6;
}

.about h2 {
  font-family: "Code Squared";
  color: #07cff6;
  font-size: 25px;
  padding: 10px 0px 5px 0px;
}

.about span {
  font-family: "Code Squared";
}

.about p {
  font-size: 16px;
  padding: 5px 0px 10px 0px;
  width: 100%;
}

.about ul {
  display: flex;
  justify-content: space-between;
}

.about li {
  list-style: none;
}

/* Card */

.card {
  margin-bottom: 35px;
  background: transparent;
}

.image-container {
  position: relative;
}

.image-container::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: #07cff6;
  opacity: 0.5;
  transition: all 0.3s linear;
}

.image-container:hover::before {
  opacity: 0;
}

.image-container img {
  width: 100%;
  height: 100%;
  min-height: 200px;
}

.hidden {
  display: none;
}

.info-container {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
}

.info-container h3 {
  font-size: 20px;
}

.links a {
  padding-left: 8px;
}

.badge {
  color: #07cff6;
  font-family: "Code Squared";
  letter-spacing: 2px;
  font-size: 9px;
  text-transform: uppercase;
}

/* Works */

#work {
  margin-bottom: 100px;
}

#work .section-title {
  text-align: right;
}

#work .section-title .title {
  animation: slideToLeft 1s linear infinite;
  animation-play-state: paused;
  animation-delay: calc(var(--scroll) * -1s);
  animation-iteration-count: 1;
  animation-fill-mode: both;
  display: inline-block;
  letter-spacing: 10px;
}

#work p {
  color: #07cff6;
  font-size: 9px;
  padding: 20px;
  font-family: "Code Squared";
  text-transform: uppercase;
}

/* contact */

#contact {
  height: 80vh;
}

#contact .container {
  display: flex;
  align-items: center;
  justify-content: center;
}

#contact div {
  width: 100%;
}

#contact .section-title {
  text-align: left;
}

#contact .section-title .title {
  animation: slideToRight 1s linear infinite;
  animation-play-state: paused;
  animation-delay: calc(var(--scroll) * -1s);
  animation-iteration-count: 1;
  animation-fill-mode: both;
  display: inline-block;
  letter-spacing: 10px;
}

#contact p.sub {
  font-size: 16px;
  color: #bebebe;
  margin-bottom: 0;
}

#contact a.email {
  color: #07cff6;
  font-size: 50px;
  font-weight: 800;
  text-decoration: underline;
}

#contact p.text {
  margin-bottom: 50px;
}

#contact .resume {
  margin-bottom: 50px;
}

/* marquee */

.marquee {
  margin: 0 auto;
  white-space: nowrap;
  overflow: hidden;
  position: absolute;
  width: -webkit-fill-available;
}

.marquee span {
  display: inline-block;
  padding-left: 100%;
  animation-delay: 2.5s;
  animation: marquee 20s linear infinite;
  font-family: "Starkiller";
  font-size: 30px;
}

/* loader */

.loaderWrapper {
  position: fixed;
  width: 100vw !important;
  height: 100vh !important;
  background: #0c0c0c;
  animation: fadeLoader 1s 3s ease forwards;
}

.loader-text {
  font-family: "Code Squared";
  font-size: 30px;
  color: #ffffff;
  text-shadow: 1px 1px 10px rgba(7, 207, 246, 1);
  margin-bottom: 0;
}

.loader {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  align-content: center;
}

@keyframes fadeLoader {
  to {
    opacity: 0;
    z-index: -1;
  }
}

@keyframes marquee {
  0% {
    transform: translate(0, 0);
  }

  100% {
    transform: translate(-100%, 0);
  }
}

@keyframes slideToRight {
  to {
    transform: translate(180px);
  }
}

@keyframes slideToLeft {
  to {
    transform: translate(-180px);
  }
}

@media (max-width: 1160px) {
  .title {
    font-size: 130px;
  }
  .image-container img {
    min-height: 150px;
  }
}

@media (max-width: 1130px) {
  #social-links {
    padding: 45px 30px 0 30px;
  }

  #emailadd {
    padding: 45px 30px 0 30px;
  }

  #top {
    padding: 35px 30px;
  }
  .image-container img {
    min-height: 150px;
  }
}

@media (max-width: 1110px) and (min-width: 992px) {
  #social-links,
  #emailadd {
    display: none;
  }
  .image-container img {
    min-height: 150px;
  }
}

@media (max-width: 1045px) {
  .title {
    font-size: 100px;
  }
}

@media (max-width: 992px) {
  #top ul li {
    font-size: 15px;
  }

  .title {
    font-size: 100px;
  }

  #hero {
    height: auto;
    margin-top: 65px;
  }
}

@media (max-width: 880px) {
  #social-links,
  #emailadd {
    display: none;
  }

  body {
    overflow-x: hidden;
  }
}

@media (max-width: 767px) {
  #hero h1 {
    font-size: 38px;
  }

  #hero h2 {
    font-size: 17px;
  }

  #hero p {
    font-size: 16px;
  }

  .image-container {
    height: auto;
  }

  #about {
    height: 100%;
  }
}

@media (max-width: 580px) {
  #hero,
  #about,
  #work,
  #contact {
    padding: 0 20px;
    margin-bottom: 80px;
  }

  #hero h1 {
    font-size: 32px;
  }

  #hero h2 {
    font-size: 16px;
  }

  #top p.logo {
    font-size: 17px;
  }

  #top ul li {
    font-size: 14px;
  }

  .title {
    font-size: 90px;
  }

  #contact a.email {
    font-size: 30px;
  }
}

